    <div>
        <h1 :class='size'> <slot> </slot> </h1>
    </div>
</template>

<script>
export default {
    props: {
        data: {
            type: String,
            required: false,
            default: 'headline'
        }
    },
    data(){
        return {
            size: {
                'headline' : this.$vuetify.breakpoint.lgAndUp,
                'headline' : this.$vuetify.breakpoint.mdOnly,
                'headline' : this.$vuetify.breakpoint.smAndDown,
            }
        }
    }
}
