<template>

<div style='' class='pt-4'> 

       <vue-easy-lightbox
         moveDisabled
         :visible="visible"
         :imgs="imgs"
         :index='index'
         @hide='handleHide'
         style='z-index: 10000000 !important;position: fixed; top: 48px; height: 100vh; width: 100vw;'

       ></vue-easy-lightbox>
       
       <v-img 
              id='top-left' 
              ::lzy-src='imgg' 
              :src='imgg+"?lazy"'
              aspect-ratio='0.577'
              width='12vw'
              min-width=60
              position='left'
              contain
              >
              </v-img>

       
       <v-layout fill-height justify-center> <!-- main -->

              <v-flex 
                     offset-lg1
                     xs10 
              > 

                     <v-layout row wrap
                            :justify-center='$vuetify.breakpoint.mdAndDown'
                            :justify-end='$vuetify.breakpoint.lgAndUp'
                     > <!-- Inner -->

                            <v-flex  
                                   xs12 
                                   
                                   lg8
                                   offset-lg4
                                   mb-2
                            >
                                   <div style=''>

                                          <v-layout row 
                                                 
                                          > 
                                                 <v-flex shrink class='ctitle-shape'>
                                                        <v-img height=40 style='transform-origin: center;transform: rotateY(45deg)' width=25 :src='bullet'> </v-img>
                                                 </v-flex>
                                                 <v-flex class='ctitle'>
                                                        <r-typo style='opacity: .5'> {{product.name}} <span class='subheading'> {{product.design_code}} </span> </r-typo>
                                                 </v-flex>

                                                 <v-flex shrink align-self-end v-if= 'true'>
                                                 </v-flex>
                                          </v-layout>   
                                   </div>

                                   <div 
                                          class='ctitle-underline'

                                          style='
                                                 width: 100%;
                                                 height: 5px;
                                                 background:  #afafaf;
                                                 opacity: .65
                                          '
                                   >

                                   </div>
                            </v-flex>
                            <v-flex 
                                   xs11
                                   sm6 
                                   md3 
                                   lg2 
                                   shrink

                            > <!-- Product left -->

                                   <v-layout 
                                          :fill-height='wrapState'
                                          row
                                          justify-center
                                          :align-start='!wrapState'
                                          :align-content-space-around='wrapState'
                                          wrap
                                   
                                   >
                                          <v-flex  shrink class='cimg' xs6 v-if='product.img_product' >  <!-- image -->
                                                 <img 
                                                        :src="product.img_product+'?lazy'"
                                                        :style="productStyle"
                                                        @click="handleExpand"
                                                        alt="product image"
                                                        
                                                        
                                                 > </img>

                                          </v-flex>

                                          <v-flex shrink class='cimg' xs12 v-if='product.img_blueprint'> <!-- blueprint -->
                                                 <img 
                                                        :src="product.img_blueprint+'?lazy'"
                                                        contain aspect-ratio=1
                                                        :style="productStyle"
                                                        class=''
                                                        min-width='10vmax'
                                                        @click="handleExpand"
                                                        alt="product blueprint"
                                                        
                                                 > </img>
                                          </v-flex>
                                   </v-layout>
                            </v-flex>

                            <v-flex 
                                   xs12  
                                   offset-xs0
                                   lg8 offset-lg2
                                   style='position: relative'> <!-- Product Content -->

                                   <container>
                                          <v-layout row wrap justify-end >

                                                
                                                 
                                                 <v-layout 
                                                        fill-height 
                                                        id='reference'
                                                        justify-end
                                                        align-top 
                                                        wrap
                                                        style='overflow-x: hidden; position: relative'
                                                        
                                                 >             
                                                        <template >
                                                               <!--
                                                                      :style='loaded ? genStyle($refs["imperial"]) : {}'
                                                                      :style='loaded ? genStyle($refs["metric"]) : {}'
                                                               -->
                                                               <v-flex grow  pa-1 class='c-table' id='imperialParent'> 
                                                                      <img class='product-image-flex' :src='product["img_imperial"]'  
                                                                             id='iimg1'
                                                                             @load='genStyle'
                                                                             @click='handleExpand'
                                                                             ref='imperial'
                                                                             
                                                                             

                                                                             @error='handleImgError("imperial")'
                                                                             
                                                                      > </img>   

                                                               </v-flex>

                                                               <v-flex grow pa-1 class='c-table' id='metricParent'> 
                                                                      <img class='product-image-flex'  :src='product["img_metric"]'
                                                                             @click='handleExpand'
                                                                             ref='metric'
                                                                             @load='genStyle'
                                                                             cursor: pointer

                                                                             @error='handleImgError("metric")'

                                                                      > </img>   
                                                               </v-flex> 
                                                        </template>


                                                               
                                                        
                                                 </v-layout> 
                                          </v-layout>
                                         

                                   </container>
                            </v-flex> 
                     </v-layout>

              </v-flex>


       </v-layout>

    

       <v-img id='br' :src='imgg' cover
              aspect-ratio='0.577' width='15vw'  style='z-index: -1;'>

       </v-img>


       <v-layout style='width: 100%; z-index: 9999 !important;background: rgb(230, 230, 230)' id='ppipe'  :pa-5='$vuetify.breakpoint.smAndDown' > 
              <v-flex xs12 style='position: relative'>

                      <pPipe v-if='$vuetify.breakpoint.mdAndUp' @loaded='pipeLoaded()'  style='background: #e6e6e6;min-height: 125px;padding-bottom: 2em'> </pPipe>


                     <v-layout row id='toMove' 
                     :style='{"position": $vuetify.breakpoint.mdAndUp ? "absolute !important" : "relative !important"}' align-center pb-3>
                            <v-layout row :wrap='$vuetify.breakpoint.smAndDown' style='' class='brandLogos' justify-end>   
                                   <v-flex grow v-for='item in brands' class='px-2 brand'> 
                                          <v-img  :src='item.img' contain width=64 aspect-ratio=1 ></v-img>
                                   </v-flex>

                            </v-layout>
                     </v-layout>
                            
              
              </v-flex>
       </v-layout>

     
</div>
</template>

<script>

import pPipe from '../helpers/product-pipe'
import rTypo from '../helpers/r-typo'
import { findKey } from 'lodash'

export default {
       name: 'product',
       props: {

              data: {
                     type: Object,
                     required: true
                     }
       },

       components: {
              pPipe,
              rTypo
       },

       methods: {
              handleExpand(e){

                     //console.log('expand')
                     

                     this.imgs=e.target.getAttribute("src")
                     console.log(this.imgs)
                     //alert('expanded '+this.imgs)
                     this.visible = true

                     e.stopPropogation()


                     
                     
              },
              setAr(e){
                     //console.log( this.imgSize.img[0] )
              },

              setBrands(e){
                     
                     
              },
              genStyle(e){
                     this.imgSize.img[0] = Math.round(e.target.width/e.target.height)

                     //alert(e.target.width)

                     
                     let dump = {}
                     let widthFactor = (window.innerWidth/e.target.width)
                     //alert(`width Factor ${widthFactor}`)

                     if(widthFactor >= 2 && this.$vuetify.breakpoint.mdAndUp)
                     {
                            dump = {
                                   "max-width": `calc( ${((window.innerWidth/2))}px - 24vw ) `
                            }
                     }
                     else
                     {
                            this.wrapState = true
                            dump =  {
                                   "width": "100%",
                                   "height": "auto",

                                   
                            }

                     }
                            
                     //console.log(e,'',2)

                     if(e)
                     {     
                            //console.log('ASSIGNED')

                            for( let prop in dump)
                            {
                                   //console.log(dump)
                                   e.target.style[prop] = dump[prop]
                            }
                                   
                            //Object.assign(e.target.style,dump)
                     }


              },
               handleHide(){
                     this.visible = false
              },
              handleImgError(arg) {

                     
                     if(arg == 'imperial')
                     {      
                            this.wrapState = false
                            let temp = document.getElementById('imperialParent')
                            if(temp)
                                   temp.innerHTML = ""
                     }
                     else if(arg == 'metric')
                     {
                            this.wrapState = false
                            let temp = document.getElementById('metricParent')
                            if(temp)
                                   temp.innerHTML = ""
                     }
                            
              },
              pipeLoaded(){

                     this.measureSystem = this.measure[0] ? this.measure[0] : (this.measure[1] ? this.measure[1] : null) 
                     var line = document.getElementById('placeholder').getBoundingClientRect()
                     
                     var slick = document.getElementById('slick')

                     if(this.$vuetify.breakpoint.mdAndUp)
                     {
                            var target = document.getElementById('toMove')
                            target.style.top += 125+'px'
                            target.style.left = line.left + 'px'
                            target.style.zIndex = 500
                            target.style.width = line.width*.5
                     }

                     

                     var tl = this.$anime.timeline({
                            loop: false,
                            autoplay: true,
                            easing: 'easeInOutQuart'
                     })

                     tl.add({

                            targets: '#w-pipe > path',
                            strokeDashoffset: [this.$anime.setDashoffset, 0],
                            duration: 2000 ,

                     })
                     
                     tl.add({
                            targets: '#g-pipe',
                            strokeDashoffset: [this.$anime.setDashoffset, 0],
                            duration: 1500 ,

                            }, '-=1500')  
                     
                     tl.add({
                            targets: '#instrument,#instrument-2',
                            opacity: [0,100]

                     },'*=.5')

                     tl.add({
                            targets: '#toMove',
                            translateY: [50,'-70%'],
                            opacity: [0,1]
                     },'*=.5')

                     
              $('.brandLogos').slick({
              
                     infinite: true,
                     loop: true,
                     speed: 300,
                     slidesToShow: 6,     
                     slidesToScroll: 1,
                     autoplay: true
                     
              });

              }
       },
       watch: {
              '$vuetify.breakpoint.smAndDown': function (val){
                     if(val == true)
                     {
                            this.productStyle.maxWidth = '25vmax'
                     }
              },
              '$vuetify.breakpoint.mdAndUp': function (val){
                     if(val == true)
                     {
                            this.productStyle.maxWidth = '12vmax'
                     }
              }
       },
       data(){
              return {
                     imgs: '',
                     index: 0,
                     visible: false,

                     //table : require('@/assets/p/table-test.png'),
                     imgg: require('@/assets/top-left.svg'),
                     bullet: require('@/assets/bullet.svg'),
                     
                     imgSize  : {
                            img: [0,0],
                            mounted : false
                     },

                     product: this.data.datalist.find( (k) => k.id == this.$route.params.pid ),
                     
                     measure: [
                     ],

                     brands: [
                            { img:  require('@/assets/certifications/abs.png')},
                            { img:  require('@/assets/certifications/dnv.png')},
                            { img:  require('@/assets/certifications/tuv_nord.png')},
                            { img:  require('@/assets/certifications/tuv_rhein.png')},
                            { img:  require('@/assets/certifications/nkk.png')},
                             { img:  require('@/assets/certifications/tuv_nord.png')},
                            
                     ],


                     slickOpt: {
                           /* infinite: true,
                            centerMode: true,
                           
                            autoplay: false,
                            slidesToShow: 4,
                            slidesToScroll: 1,
                            variableWidth: true */
                            dots: true,
                            infinite: true,
                            speed: 300,
                            slidesToShow: 1,
                           // centerMode: true,
                            variableWidth: true
              
                     },

                     

                     measureSystem: '',

                     state: false,
                     loaded: false,
                     wrapState: false,

                     productStyle: {
                            maxWidth: '13vmax !important',
                            objectFit: 'contain',
                            height: 'auto'
                     }

              
              }
       },
       computed : {
             /* product(){
                     return this.data.datalist[this.$route.params.pid]
              },
              box(){


                     let obj = []

                     if(this.data.datalist[this.$route.params.pid].img_imperial)
                            obj.img_imperial = (this.data.datalist[this.$route.params.pid].img_imperial)
                     if(this.data.datalist[this.$route.params.pid].img_metric)
                            obj.img_metric = this.data.datalist[this.$route.params.pid].img_metric

                     return obj
              }*/

              
       },

       mounted() {

              //console.log(this.data.datalist.find( (k) => k.id == this.$route.params.pid ))

              if(this.$vuetify.breakpoint.smAndDown)
              {
                     this.productStyle.maxWidth = '25vmax'
              }
              
              this.measure = [
                     this.product.img_imperial ? 'imperial' : false,
                     this.product.img_metric ? 'metric': false
              ]
              
             // alert(JSON.stringify(this.data))
              
              //console.log(` Product ID ${this.$route.params.pid} `)
              //console.log(`data -> ${JSON.stringify(this.data.datalist,'',2)}`)
              this.loaded = true

              let tl2 = this.$anime.timeline({
                     delay: 200,
                     loop: false,
                     autoplay: true,
                     easing: 'linear'
              })

              tl2.add({
                     targets: '.ctitle-underline',
                     scaleX: [0,1],
                     duration: 500,
                     easing: 'easeInOutExpo'
              })
              tl2.add({
                     targets: '.ctitle-shape , .ctitle',
                     opacity: [0,1],
                     translateX: ['-100',0],
                     duration: 500,
                     easing: 'easeInOutQuint'
                     
              }, '-=500')
              

              tl2.add({
                     targets: '.cimg ',
                     duration: 300,
                     opacity: [0,1],
                     translateX: ['-100',0],
                     easing: 'easeInOutQuint'
              },'-=650')

              tl2.add({
                     targets: '.c-table',
                     duration: 500,
                     translateX: ['100%',0],
                     opacity: [0,1],
                     easing: 'easeOutQuint'
              }, '-=650')
             
       },


}
</script>

<style>


.slick-arrow{
       filter: invert(100%) !important;
}

#toMove{
       z-index: 500;
       top: 10%;
       position: absolute;
}

#ppipe
{
       position: fixed;
       bottom: 0px;
       
}

.ctitle-underline{
       transform-origin: left;
}


#br{
       margin-left: auto;   
       margin-top: auto;
       transform: rotate(180deg);
}
       


.brand {
       filter: grayscale(100%);
       transition: all .2s linear;
       transform-origin: bottom top;

}

.brand:hover  {

       filter: grayscale(0%);
       transform: scale(1.2) ;
       transition: all .2s linear;
}


.ctable > *{
       transition: all .1s linear;
}

.ctable > *:hover{
       transform: scale(1.02);
       opacity: 100;
}



.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

       .vel-img{
              background: white !important;
       }


</style>
